.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

div.site {
  padding-top: 60px; }

/* ***********************
  basics
*********************** */
header.site_header {
  position: relative; }

header.site_header span.stripe {
  height: 60px;
  position: fixed;
  width: 100%;
  background-color: #333;
  background-image: linear-gradient(#424242 0px, #000);
  z-index: 1500;
  transition: margin 0.5s ease-in-out; }

header.site_header span.main-nav {
  position: absolute;
  left: 0;
  top: 60px; }

header.site_header li,
header.site_header li a {
  display: block;
  float: left;
  clear: left;
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%; }

a.site-title {
  display: inline-block;
  text-align: center;
  vertical-align: bottom;
  float: left;
  clear: none;
  width: 5em;
  height: 60px;
  background: url("../images/solful-fitness-logo.svg") no-repeat;
  background-position: 30% 0.125em;
  background-size: 69%; }
  .no-svg a.site-title {
    background: url("../images/solful-fitness-logo.png") no-repeat; }
  a.site-title h1 {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px; }

span.mobile_shader {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1;
  width: 100%;
  background-color: transparent;
  transition: background-color 0.4s ease-in-out; }

/* **********************
  mobile menu OFF:
********************** */
nav.site-nav {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 0;
  overflow: hidden;
  z-index: 1000;
  transition: height 0.25s ease-in-out; }
  nav.site-nav li a {
    padding: 1.2135em; }

nav.site-nav > span {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%; }

nav.site-nav span.mn,
nav.site-nav span.sn {
  display: block;
  float: left;
  clear: both;
  width: 100%; }

nav.site-nav span.mn {
  padding-top: 60px;
  margin-top: -60px;
  background-color: #FFF; }

nav.site-nav span.sn {
  padding-bottom: 60px;
  background-color: transparent; }

ul.mainNav,
ul.subNav {
  display: block;
  float: left;
  clear: left;
  width: 100%; }

ul.mainNav li a,
ul.subNav li a {
  background-color: #FFF; }

ul.subNav li a {
  background: #CCC; }

nav.site-nav a {
  color: #4d4d4d; }
  .no-touchevents nav.site-nav a:hover {
    text-decoration: none;
    color: #000;
    background: #F4F4F4; }

div.mastheadLogin {
  position: absolute;
  top: 0;
  right: 0;
  color: #FFF;
  z-index: 1;
  height: 60px;
  color: #ef4137; }
  div.mastheadLogin:hover {
    cursor: pointer; }

div.mastheadLogin a.logIn {
  display: block;
  background-color: transparent;
  padding: 0 0.40046em;
  text-align: center;
  font-size: 22px;
  line-height: 60px;
  transition: color 0.25s;
  color: #e11d12; }
  .no-touchevents div.mastheadLogin a.logIn:hover {
    color: #ef4137;
    background: transparent;
    text-decoration: none; }

div.mastheadLogin a.mbAccount {
  padding: 0.5em;
  display: block;
  font-size: 22px;
  height: 60px; }
  div.mastheadLogin a.mbAccount > span {
    display: block;
    width: 40px;
    height: 40px;
    text-align: center;
    border: 3px #c91a10 solid;
    border-top-left-radius: 50%;
    border-top-right-radius: 50%;
    border-bottom-right-radius: 50%;
    border-top-right-radius: 50%;
    border-bottom-left-radius: 50%;
    border-bottom-right-radius: 50%;
    border-bottom-left-radius: 50%;
    border-top-left-radius: 50%; }

/* **********************
  mobile menu ON:
********************** */
body.main_nav_on nav.site-nav {
  transition: height 0.5s ease-in-out; }

body.main_nav_on body > div.site {
  position: fixed; }

body.main_nav_on span.mobile_shader {
  background-color: rgba(0, 0, 0, 0.6);
  transition: background-color 0.4s ease-in-out; }

/* **********************
  masthead HIDDEN:
********************** */
body.hiddenMasthead header.site_header span.stripe {
  margin-top: -60px; }

span.toggle_menu {
  display: block;
  float: left;
  clear: none;
  width: 60px;
  height: 60px;
  background-color: transparent;
  transition: background-color 0.25s ease-in-out;
  color: #FFF; }
  span.toggle_menu .line {
    position: relative;
    background: #000;
    background: #ef4137;
    height: 3px;
    width: 32px;
    border-radius: 3px;
    margin: 6px 14px;
    transition: all 0.4s ease-in-out; }
  span.toggle_menu .line-1 {
    margin-top: 20px; }
  span.toggle_menu a.toggle {
    position: absolute;
    left: 0;
    top: 0;
    width: 60px;
    height: 60px;
    background-color: transparent;
    cursor: pointer; }

body.main_nav_on span.toggle_menu {
  background-color: #FFF; }
  body.main_nav_on span.toggle_menu .line-1,
  body.main_nav_on span.toggle_menu .line-2,
  body.main_nav_on span.toggle_menu .line-3 {
    position: relative;
    left: 0;
    transition: all 0.4s ease-in-out; }
  body.main_nav_on span.toggle_menu .line-2 {
    background: transparent; }
  body.main_nav_on span.toggle_menu .line-1, body.main_nav_on span.toggle_menu .line-3 {
    width: 29px; }
  body.main_nav_on span.toggle_menu .line-1 {
    margin: 21px 0 0 16.5px;
    transform-origin: 10% 100%;
    transform: rotateZ(45deg); }
  body.main_nav_on span.toggle_menu .line-3 {
    margin: -1.5px 0 0 19px;
    transform-origin: 10% 100%;
    transform: rotateZ(-45deg); }

@media (min-width: 45em) {
  div.site {
    padding-top: 0; }
  /* ***********************
  basics
*********************** */
  header.site_header {
    min-height: 80px; }
    header.site_header span.toggle_menu {
      display: none !important;
      visibility: hidden; }
    header.site_header li,
    header.site_header li a {
      width: auto;
      clear: none; }
  header.site_header span.stripe {
    z-index: auto;
    display: block;
    width: auto;
    height: 0;
    background-color: #CCC;
    position: relative;
    transition: margin 0;
    max-width: 75em;
    margin-left: auto;
    margin-right: auto; }
    header.site_header span.stripe::after {
      clear: both;
      content: "";
      display: block; }
  a.site-title {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 10;
    background: url("../images/solful-fitness-logo.svg") no-repeat;
    background-position: 55% 0.3125em;
    background-size: 75%;
    width: 7.5em;
    height: 6.125em; }
    .no-svg a.site-title {
      background: url("../images/solful-fitness-logo.png") no-repeat; }
  nav.site-nav {
    display: block;
    position: relative;
    float: left;
    clear: none;
    top: 0;
    width: 100%;
    background: #000;
    z-index: 0;
    min-height: 80px;
    border-bottom: none;
    overflow: visible; }
  nav.site-nav a {
    color: #F4F4F4; }
  nav.site-nav > span {
    position: absolute;
    height: auto;
    left: 0;
    top: 0;
    bottom: auto; }
  nav.site-nav span.mn {
    float: none;
    clear: none;
    width: 100%;
    padding: 0;
    background-color: transparent;
    height: 80px; }
    nav.site-nav span.mn > span {
      display: block;
      padding-left: 7.5em;
      max-width: 75em;
      margin-left: auto;
      margin-right: auto; }
      nav.site-nav span.mn > span::after {
        clear: both;
        content: "";
        display: block; }
  nav.site-nav span.mn {
    margin-top: 0;
    background-color: #333;
    background-image: linear-gradient(#424242 0px, #000); }
  ul.mainNav li a {
    background-color: transparent;
    padding: 36px 0.40046em 22px;
    line-height: 22px;
    transition: color 0.25s; }
    .no-touchevents ul.mainNav li a:hover {
      color: #cccccc;
      background: transparent; }
  div.mastheadLogin {
    height: 80px; }
  div.mastheadLogin a.logIn {
    padding: 36px 0.60675em 22px;
    line-height: 22px; }
  /* **********************
  masthead HIDDEN:
********************** */
  body.hiddenMasthead header.site_header span.stripe {
    margin-top: 0; } }

